





import "./../scss/account.scss"
import React from "react";
import ReactDOM from "react-dom";
import Ajax from "./../MyAjax.js";
import {hashHistory,Link} from "react-router"
import Alert from "./console";




export default 
class Account extends React.Component{
	constructor(props){
		super(props)
		this.state={
            goods:[],
            id:[]
        }
		
    }
    clear(){
        localStorage.removeItem("id");
         var arr=[];
           arr.push(<div key='1'  className="yonhu1"><div className="div"></div><div className="yong"> <span><Link to="/login"></Link></span>/
                    <span><Link to="/login"></Link></span></div></div>)
        this.setState({
            id:arr
        })
    }
    componentWillMount(){
        console.log(localStorage.getItem("id"))
       if(localStorage.getItem("id")){
             var arr=[];
           arr.push(<div key='1' className="yonhu1"><div className="div"></div><div className="yong"> <span>{localStorage.getItem("id")} </span>
           
          <span onClick={this.clear.bind(this)}>  <Link to="/account" ></Link></span></div></div>)
        this.setState({
            id:arr
        })
       }else{
           var arr=[];
           arr.push(<div key='1'  className="yonhu1"><div className="div"></div><div className="yong"> <span><Link to="/login"></Link></span>/
                    <span><Link to="/login"></Link></span></div></div>)
        this.setState({
            id:arr
        })
    }
    }
    back(){
        window.history.back()
    }
    shou(){
        if(!localStorage.getItem("id")){
            console.log(100,80,"")
            Alert.alert(100,80,"")
            // alert("")

        }else{
            hashHistory.push({
                pathname:"/lick"
                
            })
        }
    }
    render(){
       var arr=this.state.id
        return(
            <div className="account">
                <div className="accounthead">
                    <div className="back iconfont" onClick={this.back.bind(this)}>&#xe600;</div>
                    
                    <div className="iconfont right" ><Link to="/">&#xe621;</Link></div>
                </div>
                <div className="pink">
                    {arr}
                   
                </div>
                <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                 <div className="dai">
                    <div><Link to="/cart"></Link></div>
                    <div ></div>
                    <div></div>
                </div>
                 <div className="fa">
                     <div className="fa1">
                        <Link to="/add"><div className="left "></div>
                        <div className="right iconfont"><span></span> &#xe6c8;</div></Link>
                    </div>
                </div>
                <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                <div className="ding ding0">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                <div className="xian"></div>
                <div className="ding" onClick={this.shou.bind(this)}>
                    <div className="left" ></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                  <div className="xian"></div>
                 <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                <div className="ding">
                    <div className="left "></div>
                    <div className="right iconfont"> &#xe6c8;</div>
                </div>
                  <div className="xian"></div>
                <div className="dibu">
                    <div className="shang">
                        <a href="#/"></a>
                        <a href="#/cart"></a>
                        <a></a>
                    </div>
                    <p>400-000-1818</p>
                    <p>Copyright  2008-2017 Lefeng.com All Rights Reserved</p>
                </div>



            </div>
        )
    }
}


