





import "./../scss/pay.scss"
import React from "react";
import ReactDOM from "react-dom";
import Ajax from "./../MyAjax.js";
import {hashHistory,Link} from "react-router"
import Alert from "./console";




export default 
class Pay extends React.Component{
	constructor(props){
		super(props)
		this.state={
            add:[]
        }
		
    }
    
    componentWillMount(){
        if(localStorage.getItem("add")){
            var add=localStorage.getItem("add").split(",")
            console.log(add)
            this.setState({
                add:add
            })
        }else{
             Alert.alert(200,50,"")
           setTimeout(function(){
                hashHistory.push({
                    pathname:"/add"
                })
           },1000)
        }
        
    }
    back(){
        window.history.back()
    }
    sao(){
        $(".sao").animate({
            height:"33%"
        },2000)
        $(".saoma").css({
            display:"block"
        })
    }
    render(){
        console.log(this.props.location.query.pay)
        var add=this.state.add
       var arr=[]
       arr.push(<div key={1} className="xinxi">
            <span>{add[0]},:{add[1]}</span>
       </div>)
        return(
            <div className="pay">
                <div className="payhead">
                    <div className="back iconfont" onClick={this.back.bind(this)}>&#xe600;</div>
                    
                    <div className="iconfont right" ><Link to="/">&#xe621;</Link></div>
                </div>
                <div className="xian"></div>
              {arr}
             <div className="xian"></div>
              <div className="xinxi "></div>
              <div className="xinxi mag"><span>{this.props.location.query.pay}</span></div>
               <div className="xinxi mag"><span>0</span></div>
              <div className="xian"></div>
               <div className="xinxi"><span onClick={this.sao.bind(this)}></span></div>
               <div className="sao">
                   <img src="./img/zhifu.png" alt="" title=""/>
               </div>
               <div className="saoma"></div>
            </div>
        )
    }
}


