





import "./../scss/pay.scss"
import React from "react";
import ReactDOM from "react-dom";
import Ajax from "./../MyAjax.js";
import {hashHistory,Link} from "react-router"
import Alert from "./console";




export default 
class Add extends React.Component{
	constructor(props){
		super(props)
		this.state={
            goods:[]
        }
		
    }
    
    componentWillMount(){
        
        
    }
    componentDidMount(){
       
         var $distpicker = $('#distpicker');

            $distpicker.distpicker({
                province: '',
                city: '',
                district: ''
            });
             $('#reset').click(function () {
                $distpicker.distpicker('reset');
            });

            $('#reset-deep').click(function () {
                $distpicker.distpicker('reset', true);
            });

            $('#destroy').click(function () {
                $distpicker.distpicker('destroy');
            });

            $('#distpicker1').distpicker();

            $('#distpicker2').distpicker({
                province: '----  ----',
                city: '----  ----',
                district: '----  ----'
            });

            $('#distpicker3').distpicker({
                province: '',
                city: '',
                district: ''
            });

            $('#distpicker4').distpicker({
                placeholder: false
            });

            $('#distpicker5').distpicker({
                autoSelect: false
            });

  
    }
    back(){
        window.history.back()
    }
    save(){
        // console.log($("input").eq(0).val())
        if(!$("input").eq(0).val()==""&&!$("input").eq(1).val()==""){
            var  str=$("input").eq(0).val()+","+$("input").eq(1).val()
            var sel=$("select").eq(1).val()
            // console.log(sel)
            localStorage.setItem("add",str)
            window.history.back()
            // hashHistory.push({
            //     pathname:"/pay"
            // })
        }else{
            Alert.alert(200,50,"")
         }
    }
    render(){
       
       
        return(
            <div className="pay">
                <div className="payhead">
                    <div className="back iconfont" onClick={this.back.bind(this)}>&#xe600;</div>
                    
                    <div className="iconfont right" ><Link to="/">&#xe621;</Link></div>
                </div>
              
                <div className="addcontent">
                    <div className="xian"></div>
                    <div className="people">
                        
                        <input type="text"/>
                    </div>
                    <div className="people">
                        
                        <input type="text"/>
                    </div>
                    <div className="people">
                        
                        <select name="time" id="time">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </div>
                     <div className="xian"></div>
                     <div className="add">
                         {/*  */}
                         <form className="form-inline">
                            <div data-toggle="distpicker" id="distpicker">
                                <div className="form-group">
                                <label className="sr-only left" htmlFor="province2">Province</label>
                                
                                <select className="form-control right" id="province2" data-province="----  ----"></select>
                                </div>
                                <div className="form-group">
                                <label className="sr-only left" htmlFor="city2">City</label>
                                 
                                <select className="form-control right" id="city2" data-city="----  ----"></select>
                                </div>
                                <div className="form-group">
                                <label className="sr-only left" htmlFor="district2">District</label>
                                 /
                                <select className="form-control right" id="district2" data-district="----  ----"></select>
                                </div>
                            </div>
                            </form>
                        <div className="people1">
                        
                            <textarea rows="2" cols="3" type="text" name="address" id="address" placeholder=""></textarea>
                        </div>

                     </div>
                </div>
                <div className="dibu">
                    <div className="save" onClick={this.save.bind(this)}></div>
                </div>
                
             
            </div>
        )
    }
}


